<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="时间范围">
            <template>
              <div class="block">
                <el-date-picker
                  v-model="requestParam.search.time_range"
                  type="datetimerange"
                  align="right"
                  value-format="timestamp"
                  start-placeholder="开始日期"
                  end-placeholder="结束日期"
                  :default-time="['06:00:00', '06:00:00']"
                >
                </el-date-picker>
              </div>
            </template>
          </el-form-item>
          <el-form-item label="游戏Id">
            <el-input v-model.number="requestParam.search.game_id"></el-input>
          </el-form-item>
          <el-form-item label="房间Id">
            <el-input v-model.number="requestParam.search.room_id"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="todayTime" label="记录时间" align="center">
            <template slot-scope="scope">
              {{ scope.row.todayTime | formatDate }}
            </template>
          </el-table-column>
          <el-table-column prop="game_id" label="游戏Id" align="center">
          </el-table-column>
          <el-table-column prop="game_name" label="游戏名称" align="center">
          </el-table-column>
          <el-table-column prop="room_id" label="房间Id" align="center">
          </el-table-column>
          <el-table-column prop="room_name" label="房间名称" align="center">
          </el-table-column>
          <el-table-column prop="winLose" label="总输赢" align="center">
          </el-table-column>
          <el-table-column prop="flow" label="总流水" align="center">
          </el-table-column>
          <el-table-column prop="tax" label="总税收" align="center">
          </el-table-column>
           <el-table-column prop="winLose_diff" label="日输赢" align="center">
          </el-table-column>
          <el-table-column prop="flow_diff" label="日流水" align="center">
          </el-table-column>
          <el-table-column prop="tax_diff" label="日税收" align="center">
          </el-table-column>
        </el-table>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import moment from "moment";
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        search: {
          time_range: null,
          game_id: null,
          room_id: null
        }
      },
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      }
    };
  },
  methods: {
    getList() {
      var that = this;
      let query = {
        search: this.requestParam.search,
        page: {
          current_page: this.page.current_page,
          page_size: this.page.page_size
        }
      };
      this.$api.statistic.list_dataSummary(query).then(res => {
        if (res.data.code === 200) {
          that.page.total = res.data.data.total;
          that.page.list = res.data.data.list;
        } else {
          this.$message({
            showClose: true,
            message: "获取列表失败",
            type: "error"
          });
        }
      });
    },

    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.getList();
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.getList();
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.getList();
      }
    },
    getType(row, column) {
      let val = "";
      this.options.forEach(item => {
        if (item.value === row.type) {
          val = item.label;
        }
      });
      return val;
    },
    addColor(diff) {
      if (diff => 0) {
        return "greenCor";
      } else {
        return "redCor";
      }
    }
  },
  filters: {
    formatDate: function(value) {
      return moment(value).format("YYYY/MM/DD");
    }
  }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
.greenCor {
  font-size: larger;
  color: green;
}
.redCor {
  color: red;
}
</style>



